export function createGrid(height, width, disp) {
    var array = new Array(height);

    for (var i = 0; i < height; i++) {
        array[i] = new Array(width);
    }

    for (var j = 0; j < width; j++) {
        for (var k = 0; k < height; k++) {
            array[k][j] = disp;
        }
    }
    return array;
}