import React, { useState } from 'react'
import { createGrid } from '../creteArrey/CreateGrid'
import Board from './Board'




const Game = ({} ) => {

    const [height, setHeight] = useState(3)
    const [width, setWidth] = useState(3)
    const [combination, setCombination] = useState(3)
    const [board, setBoard] = useState(createGrid(height, width, null));
    const [xIsNext, setXisNext] = useState(false);
   
    
    
   
        
    const submitAct = (e) => {
       e.preventDefault();
       setBoard(createGrid(height, width, null))
    }

   

    return (
        <> 
         <div className='home-box'>
            <h1 className='hom-box-h1'>Tic Tac Toe</h1>
            <h2 className='home-box-h1'>Choose the type of game</h2>

            <section className='home-section'>
              <form onSubmit={ submitAct}>
              <label>height
              <input 
              type='number'
              value={height} 
              max={10}
              min={2}
              onChange={(e) => setHeight(e.target.value)}
              />
              </label>
              <label>width
              <input 
              type='number'
              max={10}
              min={2}
              value={width}
              onChange={(e) => setWidth(e.target.value)}
               />
               </label>
              <label>Winning combination
              <input 
              type='number'
              max={10}
              min={2}
              value={combination}
              onChange={(e) => setCombination(e.target.value)}
               />
               </label>
               <button type='submit' onSubmit={submitAct} >Start Game</button>
                </form>
              
            </section>
        </div>
         <div className='center' >
             <p> player: {(xIsNext ? 'O' : 'X')  }</p>
         </div>
         <Board squares={board} />
        </>
    )
}

export default Game
